.mdcareer-our-people-slider .slick-dots li button {
    display: block;
    width: 16px;
    height: 16px;
    padding: 0px;
    border: 3px solid rgb(163, 161, 161) !important;
    border-radius: 100%;
    background-color: transparent;
    text-indent: -9999px;
}

.mdcareer-our-people-slider .slick-dots li.slick-active button {
    border: 3px solid rgb(253, 86, 54) !important;
}

@keyframes expand-to-fade {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    50% {
        transform: scale(1.3);
        opacity: 0.5;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.icon-pulse-wrapper {
    position: relative;
    display: inline-block;
}

.icon-pulse-wrapper::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 40px;
    /* reduced from 60px */
    height: 40px;
    /* reduced from 60px */
    transform: translate(-50%, -50%);
    border: 2px solid rgba(255, 255, 255, 0.4);
    border-radius: 9999px;
    animation: pulse-ring 1s infinite ease-out;
    /* faster animation */
}

@keyframes pulse-ring {
    0% {
        transform: translate(-50%, -50%) scale(0.8);
        opacity: 0.6;
    }

    70% {
        transform: translate(-50%, -50%) scale(1.2);
        /* reduced scale */
        opacity: 0;
    }

    100% {
        transform: translate(-50%, -50%) scale(1.2);
        opacity: 0;
    }
}

ul.benefits li {
    position: relative;
    padding-left: 22px;
}

ul.benefits li:before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 5px;
    width: 5px;
    height: 9px;
    border: solid #fff;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
    z-index: 1;
}

ul.benefits li:after {
    content: '';
    width: 15px;
    height: 15px;
    background: #fd5636;
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
}

