/* FAQ Accordion Styles */
.faq-accordion-header {
    cursor: pointer;
    position: relative;
    padding-right: 40px;
    /* Space for copy link button */
}

.mdinc-faq-open-link {
    display: flex;
    align-items: center;
    gap: 8px;
    color: inherit;
    text-decoration: none;
}

.faq-accordion-body {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-out;
}

.faq-accordion-body.active {
    max-height: 1000px;
}

.fa-chevron-down {
    transition: transform 0.3s ease;
}

.faq-accordion-header.active .fa-chevron-down {
    transform: rotate(180deg);
}

/* Copy Link Styles */
.copy-clipboard-link-box {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: #666;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    transition: all 0.3s ease;
}

.copy-clipboard-link-box:hover {
    color: #333;
}

.copy-clipboard-link-box .fa-link {
    font-size: 14px;
}

.copy-clipboard-link-box .copied-tooltip {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    background: #333;
    color: white;
    font-size: 12px;
    padding: 4px 8px;
    border-radius: 4px;
    white-space: nowrap;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    z-index: 10;
}

.copy-clipboard-link-box .copied-tooltip:after {
    content: '';
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #333 transparent;
}

.copy-clipboard-link-box.active .fa-link {
    color: #4CAF50;
}

.copy-clipboard-link-box.active .copied-tooltip {
    opacity: 1;
    visibility: visible;
    animation: fadeInOut 2s;
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
        visibility: hidden;
    }

    20% {
        opacity: 1;
        visibility: visible;
    }

    80% {
        opacity: 1;
        visibility: visible;
    }

    100% {
        opacity: 0;
        visibility: hidden;
    }
}